.Intro {
    display: flex;
    height: 77vh;
    margin-top:7rem;
}

/* left side styling */
.i-left {
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 1.3rem;
}
.i-name {
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
}
.i-name > :nth-child(1) {
    color: var(--black);
    font-weight: bold;
    font-size: 3rem;
}
.i-name > :nth-child(2) {
    color: var(--orange);
    font-weight: bold;
    font-size: 3rem;
}
.i-name > :nth-child(3) {
    font-weight: 100;
    font-size: 14px;
    color: var(--gray);

}
.i-name > :nth-child(4) {
    font-weight: 100;
    font-size: 14px;
    color: var(--gray);
    margin-top: 10px;
}
.i-button {
    width: 6rem;
    height: 2.3rem;
}
.i-icons {
    margin-top: 1rem;
    display: flex;
    gap: 0rem;
}
.i-icons > * {
    transform: scale(0.5);
}
.i-icons > *:hover {
    cursor: pointer;
}
/* right side styling */

.i-right {
    flex: 1;
    position: relative;
}
.i-right > * {
    position: absolute;
    z-index: 1;
}
.i-right > :nth-child(1) {
    transform: scale(0.98);
    left: -15%;
    top: -10%;
}
.i-right > :nth-child(2) {
    top: -4.6rem;
    transform: scale(0.74);
    left: -3rem;
    width: 35rem;
}
.i-right > :nth-child(3) {
    transform: scale(1.4);
    left: 17%;
    margin-top: -50px;
}
.i-right > :nth-child(4) {
    transform: scale(0.62);
    top: -19%;
    left: -24%;
    border-radius: 50%;
    padding: 0;
}
.i-right > :nth-child(7) {
    width: 6rem;
    transform: scale(1.4);
    margin-top: 60px;
}
/* blur */
.blur {
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    filter: blur(212px);
    z-index: -9;
    top: -18%;
    left: 56%;
    filter: blur(72px);
}

@media screen and (max-width: 480px) {
    .Intro {
        flex-direction: column;
        gap: 7rem;
        height: 64rem;
        margin-top: 60px;
    }

    .i-right {
        transform: scale(0.68);
        left: -2rem;
        margin-top: -12rem;
    }
    .i-right .blur {
        display: none;
    }
    .floating-div:nth-of-type(1){
        /* display: none; */
        top: -7rem !important;;
    }
    .i-right > :nth-child(7) {

        margin-left:6rem;
        margin-top: 60px;
    }
}
